.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: black;
  color: white;
}

.App-header {
  background-image: url(./imgs/home.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#herman-picture{
  background-image: url(./imgs/trees.jpg);
  width: 300px;
  height: 300px;
  background-position: center;
  background-size: 90%;
  border-radius: 100%;
}
.herman-info-box{
  width: 680px;
  margin-left: 20px;
}
.intro{
  width: 1000px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-evenly;
  padding: 20px;
  background-color: rgb(80, 80, 80);
}
.intro:hover{
  background-color: rgb(80, 80, 80);
  
}
.intro-box{
  height: auto;
  background-color: rgb(80, 80, 80);
  width: 100%;
  color: rgb(150, 150, 150);
  transition: 0.7s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
}
.intro-box:hover{
  background-color: rgb(0, 0, 0);
  transition: 0.7s;
  color: white;
}
#subtitle{
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 1vmin);
  font-weight: 700;
}
#title{
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-size: calc(45px + 1vmin);
  font-weight: 700;
  padding-bottom: 15px;
}

.title-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  position: relative;
}


@media (max-width: 500px) {
  #subtitle{
  font-size: calc(10px + 1vmin);
  justify-content: flex-start;
  }
  #title{
    font-size: calc(25px + 1vmin);
    text-align: center;
  }
  .title-card{
    height: auto;
  }
}
@media (max-width: 999px) {
  #herman-picture{
    width: 250px;
    height: 250px;
  }
  .herman-info-box{
    width: 90%;
  }
  .intro{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(80, 80, 80);
  }
  .intro-box{
    height: auto;
    background-color: rgb(80, 80, 80);
    width: 100%;
    color: rgb(150, 150, 150);
    transition: 0.7s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

